@mixin telefono-pequeño {
  @media (max-width: $telefono) {
    @content;
  }
}
@mixin telefono {
  @media (max-width: $telefono) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: $tablet) {
    @content;
  }
}

@mixin desktop {
  @media (max-width: $desktop) {
    @content;
  }
}
