* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-family: "Roboto", sans-serif;
  scroll-behavior: smooth;
  height: 100%;
  overflow-x: hidden; }

section {
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  padding: 0 40px;
  align-items: center;
  flex-direction: row; }

section:nth-child(2) {
  background-image: linear-gradient(to top, #000000 0%, #537895 100%);
  z-index: -5; }

section:nth-child(3) {
  background-image: linear-gradient(to top, #000000 0%, #537895 100%);
  z-index: -5; }

section:nth-child(4) {
  background-image: linear-gradient(to top, #000000 0%, #537895 100%);
  z-index: -5; }

section:nth-child(5) {
  /* background-color: black; */
  background-image: linear-gradient(to top, #000000 0%, #537895 100%);
  z-index: 1; }

.parallax {
  background: #fff fixed no-repeat 50% 50%;
  background-size: cover; }

.animado {
  opacity: 0;
  transition: all 0.5s; }

::-webkit-scrollbar {
  width: 1%; }

::-webkit-scrollbar-track {
  background-color: #616161; }

::-webkit-scrollbar-thumb {
  background: #537895;
  border-radius: 50px; }

