.lds-roller {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px; }

.lds-roller div {
  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px; }

.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #fff;
  margin: -4px 0 0 -4px; }

.lds-roller div:nth-child(1) {
  animation-delay: -0.036s; }

.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px; }

.lds-roller div:nth-child(2) {
  animation-delay: -0.072s; }

.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px; }

.lds-roller div:nth-child(3) {
  animation-delay: -0.108s; }

.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px; }

.lds-roller div:nth-child(4) {
  animation-delay: -0.144s; }

.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px; }

.lds-roller div:nth-child(5) {
  animation-delay: -0.18s; }

.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px; }

.lds-roller div:nth-child(6) {
  animation-delay: -0.216s; }

.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px; }

.lds-roller div:nth-child(7) {
  animation-delay: -0.252s; }

.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px; }

.lds-roller div:nth-child(8) {
  animation-delay: -0.288s; }

.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px; }

@keyframes lds-roller {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.loader {
  background-color: black;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1000;
  clip-path: circle(140.9% at 0 0);
  transition: clip-path 2s ease-in-out; }

.loader2 {
  clip-path: circle(0% at 0 0);
  transition: all 0.3s ease-in-out; }

/* -----------------------------
 ------------navbar------------
 ----------------------------- */
#nav-container {
  z-index: 500;
  position: fixed;
  height: 100vh;
  width: 100%;
  pointer-events: none; }
  #nav-container .bg {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100vh;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s;
    background: #000; }
  #nav-container .button {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 200;
    -webkit-appearance: none;
    border: 0;
    background: transparent;
    border-radius: 0;
    height: 70px;
    width: 30px;
    cursor: pointer;
    pointer-events: auto;
    margin-left: 25px;
    touch-action: manipulation;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
    #nav-container .button .icon-bar {
      display: block;
      width: 100%;
      height: 3px;
      background: #aaa;
      transition: 0.3s; }
    #nav-container .button .icon-bar + .icon-bar {
      margin-top: 5px; }
  #nav-container #nav-content {
    padding: 40px;
    width: 300px;
    position: absolute;
    height: 100vh;
    background: #22637b;
    pointer-events: auto;
    -webkit-tap-highlight-color: 900;
    transform: translateX(-100%);
    transition: transform 0.3s;
    will-change: transform;
    contain: paint; }
    @media (max-width: 600px) {
      #nav-container #nav-content {
        width: 230px; } }
    #nav-container #nav-content ul {
      height: 100%;
      display: flex;
      flex-direction: column; }
    #nav-container #nav-content li a {
      margin-top: 20px;
      padding: 10px 5px;
      font-size: 1.5rem;
      display: block;
      text-transform: uppercase;
      transition: color 0.1s;
      color: #ffffff; }
      @media (max-width: 600px) {
        #nav-container #nav-content li a {
          font-size: 1rem; } }
      #nav-container #nav-content li a:hover {
        color: coral; }
    #nav-container #nav-content .small {
      position: absolute;
      bottom: 3%;
      display: flex;
      align-self: center; }
      #nav-container #nav-content .small a {
        font-size: 12px;
        font-weight: 400;
        color: white; }
    #nav-container #nav-content .small a + a {
      margin-left: 15px; }
  #nav-container a,
  #nav-container a:visited,
  #nav-container a:focus,
  #nav-container a:active,
  #nav-container a:link {
    text-decoration: none;
    outline: 0; }
  #nav-container a {
    color: currentColor;
    transition: 0.2s ease-in-out; }
  #nav-container ul {
    padding: 0;
    list-style: none; }

#nav-container:focus-within .bg {
  visibility: visible;
  opacity: 0.6; }

#nav-container * {
  visibility: visible; }

#nav-container:focus-within .button {
  pointer-events: none; }

#nav-container:focus-within .icon-bar:nth-of-type(1) {
  transform: translate3d(300px, 8px, 0) rotate(45deg); }
  @media (max-width: 600px) {
    #nav-container:focus-within .icon-bar:nth-of-type(1) {
      transform: translate3d(210px, 8px, 0) rotate(45deg); } }

#nav-container:focus-within .icon-bar:nth-of-type(2) {
  transform: translate3d(300px, 0, 0);
  opacity: 0; }
  @media (max-width: 600px) {
    #nav-container:focus-within .icon-bar:nth-of-type(2) {
      transform: translate3d(210px, 0, 0); } }

#nav-container:focus-within .icon-bar:nth-of-type(3) {
  transform: translate3d(300px, -8px, 0) rotate(-45deg); }
  @media (max-width: 600px) {
    #nav-container:focus-within .icon-bar:nth-of-type(3) {
      transform: translate3d(210px, -8px, 0) rotate(-45deg); } }

#nav-container:focus-within #nav-content {
  transform: none; }

